import { Fragment, FunctionComponent } from 'react';
import { useTranslation } from 'react-i18next'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Media, Row, Col, Card, Table } from 'react-bootstrap';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import MetaInfo from '../../components/MetaInfo';

import emoji_list from '../../assets/emoji.json';

interface Emoji {
  name: string;
  image_urls: string[];
  code: string;
};

const AboutLink: FunctionComponent<{ title: string, href: string }> = ({ title, href }) => {
  return (
    <a target='_blank' rel='noopener noreferrer' title={title} href={href}>{title}</a>
  );
};

const AboutInfo: FunctionComponent = () => {
  const { t } = useTranslation();
  return (
    <Fragment>
      <Row>
        <Col>
          <Card className="mt-2 mb-2">
            <Card.Body>
              <Media>
                <img
                  width={32}
                  height={32}
                  className="mr-3"
                  src="/img/CgDfX7qcJM_J-APK1YKgCA.png"
                  alt={t('pages.about.title')}
                />
                <Media.Body>
                  <h5>{t('pages.about.title')}</h5>
                  <p>
                    {t('pages.about.about_us.1')}
                  </p>
                  <p>
                    {t('pages.about.about_us.2')}<AboutLink title='Kazakura Mirea' href='https://twitter.com/Kazakura_Mirea' />.
                  </p>
                  <p>
                    {t('pages.about.about_us.3')}<AboutLink title='GitHub Issues' href='https://github.com/mirea-button/mirea-button.github.io/issues' />.
                  </p>
                </Media.Body>
              </Media>
            </Card.Body>
          </Card>
        </Col>
      </Row>
      <Row>
        <Col>
          <Card className="mt-2 mb-2">
            <Card.Body>
              <Media>
                <img
                  width={50}
                  height={50}
                  className="mr-3"
                  src="/android-chrome-512x512.png"
                  alt={t('site.kazakura_mirea')}
                />
                <Media.Body>
                  <h5>{t('site.kazakura_mirea')}</h5>
                  <p>
                    {t('pages.about.about_mirea.1')}
                  </p>
                  <div>
                    {t('pages.about.about_mirea.2')}
                    <ul>
                      <li><AboutLink title='Youtube' href='https://www.youtube.com/channel/UCIumx9FItlv6B_JsHVMjVYA' /></li>
                      <li><AboutLink title='Twitter' href='https://twitter.com/Kazakura_Mirea' /></li>
                      <li><AboutLink title='Streamlabs' href='https://streamlabs.com/mireakazakura/tip' /></li>
                      <li><AboutLink title='FANBOX、開設🌸' href='https://kazakura-mirea.fanbox.cc' /></li>
                    </ul>
                  </div>
                </Media.Body>
              </Media>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Fragment>
  );
}

const About: FunctionComponent = () => {
  const { t } = useTranslation();
  const metaInfo = {
    title: `${t('pages.about.title')} | ${t('site.title')}`,
    description: t('pages.about.description'),
    seo: {
      image: `${process.env.REACT_APP_GITHUB_URL}/${t('pages.home.image')}`,
      url: `${process.env.REACT_APP_GITHUB_URL}/about`,
      siteUpdatedAt: new Date(),
    }
  };
  return (
    <Fragment>
      <MetaInfo {...metaInfo} />
      <AboutInfo />
      <Row>
        <Col>
          <Card className="mt-2 mb-2">
            <Card.Body>
              <Card.Title>Emoji</Card.Title>
              <Row>
                <Col xs lg="6">
                  <Table bordered hover responsive>
                    <thead>
                      <tr>
                        <th>#</th>
                        <th>Code</th>
                      </tr>
                    </thead>
                    <tbody>
                      {emoji_list.map((emoji: Emoji) => (
                        <tr key={emoji.name}>
                          <td>
                            {emoji.image_urls.map((url: string) => (
                              url.indexOf('img/') >= 0 ? (
                                <img
                                  key={url}
                                  width={32}
                                  height={32}
                                  className="mr-3"
                                  src={url}
                                  alt={emoji.code}
                                />) : (<span key={url}>{url}</span>)
                            ))}
                          </td>
                          <td>
                            <CopyToClipboard text={emoji.code}>
                              <code role="button">{emoji.code}</code>
                            </CopyToClipboard>
                            {' '}
                            <CopyToClipboard text={emoji.code}>
                              <FontAwesomeIcon title="Copy code" role="button" icon={['far', 'copy']} />
                            </CopyToClipboard>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </Col>
              </Row>
            </Card.Body>
            {/* <Card.Img variant="bottom" src="/img/og-image.jpg" /> */}
          </Card>
        </Col>
      </Row>
    </Fragment>
  );
}

export default About;
