import { memo } from 'react';
import Helmet from 'react-helmet';

type MetaInfoProps = {
  title: string,
  description: string,
  seo: any,
}

const MetaInfo = memo<MetaInfoProps>(({ title, description, seo }) => (
  <Helmet>
    <title>{title}</title>
    <link rel="icon" href={`${process.env.REACT_APP_GITHUB_URL}/favicon.ico`} />
    <link rel="manifest" href={`${process.env.REACT_APP_GITHUB_URL}/manifest.json`} />
    <link rel="apple-touch-icon" sizes="180x180" href={`${process.env.REACT_APP_GITHUB_URL}/apple-touch-icon.png`} />
    <link rel="icon" type="image/png" sizes="32x32" href={`${process.env.REACT_APP_GITHUB_URL}/favicon-32x32.png`} />
    <link rel="icon" type="image/png" sizes="16x16" href={`${process.env.REACT_APP_GITHUB_URL}/favicon-16x16.png`} />

    <meta name="description" content={description} />
    <meta name="image" content={seo.image} />
    <meta name="site-updated-at" content={seo.siteUpdatedAt} />
    <meta property="og:title" content={title} />
    <meta property="og:url" content={seo.url} />
    <meta property="og:description" content={description} />
    <meta property="og:image" content={seo.image} />
    <meta property="og:type" content="website" />
    <meta property="og:image:alt" content={description} />
    <meta name="twitter:card" content="summary_large_image" />
    <meta name="twitter:title" content={title} />
    <meta name="twitter:url" content={seo.url} />
    <meta name="twitter:description" content={description} />
    <meta name="twitter:image" content={seo.image} />
    <meta name="twitter:image:alt" content={description} />
    <link rel="canonical" href={seo.url} />
  </Helmet>
));

MetaInfo.displayName = 'MetaInfo';

export default MetaInfo;
