import { FunctionComponent, Fragment, createRef, useMemo } from 'react';
import { useTranslation } from 'react-i18next';

import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';

import AudioButton from './AudioButton';

import voice_lists from '../assets/voices.json';

interface Voice {
  name: string;
  path: string;
  description: { [index: string]: any; };
};

interface VoiceGroup {
  group_name: string;
  group_description: { [index: string]: any; };
  voice_list: Voice[];
};

interface VoiceGroups {
  groups: VoiceGroup[];
};

const getDescription = (desc: any, lng: string) => {
  if (lng === 'en' || lng.startsWith('en-')) {
    return desc.en;
  }
  if (lng === 'ja' || lng.startsWith('ja-')) {
    return desc.ja;
  }
  return desc[lng];
}

const Buttons: FunctionComponent = () => {
  const { t, i18n } = useTranslation();
  const currentLng: string = `${i18n.language}`;
  const voiceGroups: VoiceGroups = voice_lists;
  const groups: VoiceGroup[] = voiceGroups.groups;

  const overlap = false;
  const random = false;
  const repeat = false;
  const nowPlaying = new Set();

  const totalButtons = groups.map(g=>g.voice_list).reduce((v, i) => v.concat(i), []).map(v => v.name);
  const totalButton = totalButtons.length;
  const refs= useMemo(
    () => Array.from({ length: totalButton }).map(() => createRef()),
    [totalButton]
  );

  const play = (item: Voice) => {
    const ref: any = refs[totalButtons.indexOf(item.name)].current;
    let timer: any = null;
    if (!overlap) {
      nowPlaying.forEach((i: any) => {
        i.pause();
        nowPlaying.delete(i);
        console.log(item.name, 'paused before new playing');
      });
    }
    let setup_timer = () => {
      if (timer !== null) clear_timer();
      timer = setInterval(() => {
        let prog = Number(((audio.currentTime / audio.duration) * 100).toFixed(2));
        if (prog !== Infinity && !isNaN(prog)) {
          ref.updateProgress(prog);
        } else {
          ref.updateProgress(0);
        }
      }, 50);
    };
    let smooth_end = () => {
      let play_end_timer: any = setInterval(() => {
        ref.updateProgress(ref.getProgress() - 5);
        if (ref.getProgress() <= 0) {
          clearInterval(play_end_timer);
          play_end_timer = null;
        }
      }, 50);
      ref.updatePlaying(false);
    };
    let clear_timer = () => {
      clearInterval(timer);
      timer = null;
    };
    let audio = new Audio(`/voices/${item.path}`);
    audio.load(); //This could fix iOS playing bug
    if (navigator.mediaSession) {
      const metadata = {
        title: overlap ? t('control.overlap_title') : getDescription(item.description, currentLng),
        artist: t('control.full_name'),
        album: t('site.title'),
        artwork: [{ src: `/img/media-cover.png`, sizes: '512x512', type: 'image/png' }]
      };
      navigator.mediaSession.metadata = new MediaMetadata(metadata);
      navigator.mediaSession.playbackState = 'playing';
    }
    audio.addEventListener('canplay', () => {
      audio.volume = 1;
      audio.play();
      nowPlaying.add(audio);
      ref.updatePlaying(true);
      setup_timer();
    });
    audio.addEventListener('ended', () => {
      if (repeat) {
        audio.play();
        nowPlaying.add(audio);
        ref.updatePlaying(true);
        setup_timer();
      } else if (random) {
        // play_random_voice();
      } else {
        smooth_end();
        clear_timer();
        nowPlaying.delete(audio);
      }
    });
    audio.addEventListener('pause', () => {
      // console.log(item.name, 'paused');
      smooth_end();
      //if (!this.repeat) {
      clear_timer();
      nowPlaying.delete(audio);
      //}
      if (navigator.mediaSession) {
        navigator.mediaSession.playbackState = 'paused';
      }
    });
    // $bus.$on('abort_play', () => {
    //   audio.pause();
    //   smooth_end();
    //   clear_timer();
    //   nowPlaying.delete(audio);
    //   delete audio;
    // });
  };

  return (
    <Fragment>
      {groups.map((group: VoiceGroup) => (
        <Accordion key={group.group_name} defaultActiveKey="0" className="mt-2 mb-2">
          <Card>
            <Accordion.Toggle as={Card.Header} eventKey="0">
              {getDescription(group.group_description, currentLng) || ''}
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
              <Card.Body>
                {group.voice_list.map((voice: Voice) => (
                  <AudioButton key={voice.name}
                    name={voice.name}
                    path={voice.path}
                    description={getDescription(voice.description, currentLng) || ''}
                    onClick={() => play(voice)}
                    ref={refs[totalButtons.indexOf(voice.name)]}
                  ></AudioButton>
                ))}
              </Card.Body>
            </Accordion.Collapse>
          </Card>
        </Accordion>
      ))}
    </Fragment>
  );
};

export default Buttons;
