import { FunctionComponent, Fragment } from 'react';

import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import clip_lists from '../assets/clips.json';

interface Clip {
  name: string;
  url: string;
  description: { [index: string]: any; };
  source: string;
};

const Clips: FunctionComponent = () => {
  const clips: Clip[] = clip_lists;

  return (
    <Fragment>
      <Accordion defaultActiveKey="0" className="mt-2 mb-2">
        <Card>
          <Accordion.Toggle as={Card.Header} eventKey="0">
            Clips
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <Row>
                {clips.map((clip: Clip) => (
                  <Col xs={12} md={4} key={clip.name}>
                    <div className="embed-responsive embed-responsive-16by9">
                      <iframe title={clip.name} className="embed-responsive-item" src={clip.url.replace("watch?v=", "embed/")}></iframe>
                    </div>
                  </Col>
                ))}
              </Row>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </Fragment>
  );
};

export default Clips;
